.header {
  width: 80vw;
  height: 100vh;
  background-color: rgb(42, 42, 42);

  & .site-title {
    padding: 2rem;
    padding-top: 0px;
    margin: 0;
    position: relative;

    &::before{
      content: '';
      background-color: white;
      width: 20vw;
      height: 2px;
      display: inline-block;
      position: absolute;
      bottom: 2rem;
    }
  }
}